// frontend/src/api.js

// Base URL of backend, injected from Netlify environment variable
// Example: https://password-reset-yn39.onrender.com
const API_BASE_URL = process.env.REACT_APP_API_URL;

// Helper: handle fetch responses
async function handleResponse(res) {
  if (!res.ok) {
    const err = await res.json().catch(() => ({}));
    throw new Error(err.error || res.statusText);
  }
  return res.json();
}

// =============================
// Auth APIs
// =============================

// Register new user
export async function registerUser(userData) {
  const res = await fetch(`${API_BASE_URL}/api/auth/register`, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify(userData),
  });
  return handleResponse(res);
}

// Login
export async function loginUser(email, password) {
  const res = await fetch(`${API_BASE_URL}/api/auth/login`, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify({ email, password }),
  });
  return handleResponse(res);
}

// Request password reset (sends reset email)
export async function requestPasswordReset(email) {
  const res = await fetch(`${API_BASE_URL}/api/auth/reset-password`, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify({ email }),
  });
  return handleResponse(res);
}

// Reset password (using token sent in email)
export async function resetPassword(token, newPassword) {
  const res = await fetch(`${API_BASE_URL}/api/auth/reset-password/${token}`, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify({ password: newPassword }),
  });
  return handleResponse(res);
}
// default export for compatibility with existing imports
const API = { registerUser, loginUser, requestPasswordReset, resetPassword };
export default API;
