// src/components/ResetPassword.jsx
import React, { useState, useEffect } from "react";
import { useParams, useNavigate } from "react-router-dom";
import API from "../api";
import AlertMessage from "./AlertMessage";

export default function ResetPassword() {
  const { token } = useParams();
  const navigate = useNavigate();
  const [password, setPassword] = useState("");
  const [alert, setAlert] = useState({ type: "", message: "" });
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    // non-blocking validation
    async function validate() {
      try {
        await API.get(`/auth/reset-password/${token}`);
      } catch (err) {
        console.warn("Token validation failed:", err.response?.data?.message);
        // still allow form submit
      }
    }
    validate();
  }, [token]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setAlert({ type: "", message: "" });
    try {
      const res = await API.post(`/auth/reset-password/${token}`, { password });
      setAlert({ type: "success", message: res.data.message || "Password has been reset" });
      setTimeout(() => navigate("/login"), 2000);
    } catch (err) {
      setAlert({ type: "danger", message: err.response?.data?.message || "Error resetting password" });
    } finally { setLoading(false); }
  };

  return (
    <div className="form-center">
      <div className="auth-card">
        <h1 className="h1-hero">Reset Password</h1>

        <form onSubmit={handleSubmit}>
          <div className="mb-3">
            <label className="form-label">New Password</label>
            <input type="password" className="form-control" required minLength={6}
              value={password} onChange={(e) => setPassword(e.target.value)} disabled={loading} />
          </div>

          <button className="btn btn-primary w-100" disabled={loading}>
            {loading ? "Resetting..." : "Reset Password"}
          </button>
        </form>

        <AlertMessage {...alert} />
      </div>
    </div>
  );
}
