// src/components/Login.jsx
import React, { useState } from "react";
import API from "../api";
import AlertMessage from "./AlertMessage";

export default function Login() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [alert, setAlert] = useState({ type: "", message: "" });
  const [loading, setLoading] = useState(false);

  const submit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setAlert({ type: "", message: "" });
    try {
      const res = await API.post("/auth/login", { email, password });
      setAlert({ type: "success", message: res.data.message || "Logged in" });
    } catch (err) {
      setAlert({ type: "danger", message: err.response?.data?.message || "Login failed" });
    } finally { setLoading(false); }
  };

  return (
    <div className="form-center">
      <div className="auth-card">
        <h1 className="h1-hero">Login</h1>
        <form onSubmit={submit}>
          <div className="mb-3">
            <label className="form-label">Email</label>
            <input type="email" className="form-control" required value={email}
              onChange={(e) => setEmail(e.target.value)} />
          </div>

          <div className="mb-3">
            <label className="form-label">Password</label>
            <input type="password" className="form-control" required value={password}
              onChange={(e) => setPassword(e.target.value)} />
          </div>

          <button className="btn btn-primary w-100" disabled={loading}>
            {loading ? "Signing in..." : "Login"}
          </button>
        </form>

        <AlertMessage {...alert} />
        <div className="footer-note">Forgot your password? <a href="/forgot-password">Reset it</a></div>
      </div>
    </div>
  );
}
