// src/components/Register.jsx
import React, { useState } from "react";
import validator from "validator";
import API from "../api";
import AlertMessage from "./AlertMessage";

const isValidEmail = (value) => {
  if (!value) return false;
  // validator.isEmail is robust and will reject weird TLDs like ".commhfgfhfh"
  return validator.isEmail(value, { allow_utf8_local_part: false });
};

export default function Register() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [alert, setAlert] = useState({ type: "", message: "" });
  const [loading, setLoading] = useState(false);
  const [touched, setTouched] = useState({ email: false, password: false });

  const emailValid = isValidEmail(email.trim());
  const passwordValid = password.length >= 6;

  const submit = async (e) => {
    e.preventDefault();
    setTouched({ email: true, password: true });
    setAlert({ type: "", message: "" });

    if (!emailValid) {
      setAlert({ type: "danger", message: "Please enter a valid email address." });
      return;
    }
    if (!passwordValid) {
      setAlert({ type: "danger", message: "Password must be at least 6 characters." });
      return;
    }

    setLoading(true);
    try {
      const res = await API.post("/auth/register", { email: email.trim(), password });
      setAlert({ type: "success", message: res.data.message || "Registered successfully" });
      setEmail("");
      setPassword("");
      setTouched({ email: false, password: false });
    } catch (err) {
      const serverMsg = err?.response?.data?.message || err?.response?.data?.errors?.[0]?.msg;
      setAlert({ type: "danger", message: serverMsg || "Registration failed" });
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="form-center">
      <div className="auth-card">
        <h1 className="h1-hero">Register</h1>

        <form onSubmit={submit} noValidate>
          <div className="mb-3">
            <label className="form-label">Email</label>
            <input
              type="email"
              className={`form-control ${touched.email && !emailValid ? "is-invalid" : ""}`}
              required
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              onBlur={() => setTouched((t) => ({ ...t, email: true }))}
              placeholder="you@example.com"
            />
            {touched.email && !emailValid && (
              <div className="invalid-feedback" style={{ display: "block" }}>
                Please enter a valid email address (e.g. you@example.com).
              </div>
            )}
          </div>

          <div className="mb-3">
            <label className="form-label">Password</label>
            <input
              type="password"
              className={`form-control ${touched.password && !passwordValid ? "is-invalid" : ""}`}
              required
              minLength={6}
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              onBlur={() => setTouched((t) => ({ ...t, password: true }))}
            />
            {touched.password && !passwordValid && (
              <div className="invalid-feedback" style={{ display: "block" }}>
                Password must be at least 6 characters.
              </div>
            )}
          </div>

          <button className="btn btn-primary w-100" disabled={loading || !emailValid || !passwordValid} type="submit">
            {loading ? "Registering..." : "Register"}
          </button>
        </form>

        <AlertMessage {...alert} />
        <div className="footer-note">
          Already have an account? <a href="/login">Login</a>
        </div>
      </div>
    </div>
  );
}
